.profile{
    display: flex;
    margin: 10px 10px 10px 0;
    display: grid;
    grid-template-rows: 0.2fr 1fr;
  }
  
  .profile_img{
    width: 80px;
    height: 80px;
  }
  
  .profile_img img{
    width: 100%;
    height: 100%;
  }
  
 
  .profile_description{
    margin: 0 0 0 15px;
  }
  
  .profile_name{
    font-size: 20px;
    font-weight: 900;
  }

  .profile_text{
      
  }

  .create_post{
    margin: 0 0 10px 0;
  }

  .text_input{
    height: 40px;
    width: 100%;
    display: block;
    margin: 0 0 5px 0;
  }

  .send_btn{
    color: white;
    background-color: red;
    text-transform: uppercase;
    width: 70px;
    height: 20px;
  }

  .posts{
    margin: 20px 0 0 10px;
}

.posts_header{
    font-size: 20px;
    font-weight: 700;
}

.posts_item{
    display: flex;
    align-items: center;
    padding: 5px;
    margin: 0 0 10px 0;
}

.item_img_wrapper{
    width: 60px;
    height: 60px;
    border-radius: 50%;
    overflow: hidden;
    margin: 0 10px 0 0;
 }

.item_img{
    width: 60px;
    height: 60px;
}

.item_text{
    font-size: 20px;
}

.content_img{
  width: 100%;
  object-fit: cover;
  max-height: 150px;
  overflow: hidden;
}

.content_img img{
  width: 100%;
  max-height: 100%;
}

.img_wrapper{
  max-height: 200px;
  margin-bottom: 20px;
}