.friends_wrapper{
    margin: 0 0 0 20px;
    max-width: 150px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.img_wrapper{
    width: 35px;
    height: 35px;
    border-radius: 50%;
    overflow: hidden;
}

.img_wrapper img{
    width: 35px;
    height: 35px;
}

.name{
    text-decoration: none;
    color: white;
    font-size: 12px;
}

.item{
    color: white;
    text-decoration: none;
    font-size: 12px;
}

.active{
    color: gold;
    text-decoration: none;
    font-size: 12px;
}