body{
  height: 100%;
}
#root{
  min-height:100%;
}
.App{
  min-height: 100%;
  width: 100%;
}

p{
  margin: 0;
}

.wrapper{
  max-width: 1200px;
  min-height: 100%;
  margin: 0 auto;
  display: grid;
  grid-template-rows: 80px 1fr 50px;
  grid-template-columns: 2fr 10fr;
  grid-gap: 10px;
  grid-template-areas:
  'header header'
  'sidebar content'
  'footer footer';
}

.content{
  grid-area: content;
  height: 100%;
}





